import React, { Component } from 'react';
import Profile from './../assets/images/profile.png';
export default class Header extends Component {
    render() {
        return (
            <div className="profile__header">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12 col-md-12 col-sm-12 col-12">
                            <div className="row">
                                <div className="col-lg-2 col-md-2 col-sm-2 col-12">
                                    <div className="profile_pic">
                                        <img src={Profile} alt="profile" className="img-responsive profile_image" />
                                    </div>
                                </div>
                                <div className="col-lg-8 col-md-8 col-sm-8 col-12 col-lg-push-1">
                                    <div className="profile__info">
                                        <p className="profile__title">Title</p>
                                        <p className="profile__name">Name</p>
                                        <p className="profile__description">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}
